import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default function createStore () {
  return new Vuex.Store({
    state: {
      todoItems: [
        { id: 1, content: 'CSR ', activation: true },
        { id: 2, content: 'CSR  ', activation: false },
        { id: 3, content: 'SSR ', activation: false },
      ],
    },
    mutations: {
      SET_TODO_ITEMS (state, todoItems) {
        state.todoItems = todoItems;
      }
    },
  })
};

